<template>
  <div class="upload">
      <yd-layout>
        <h1 class="title">Upload your room</h1>

 <yd-cell-group title="Your house pictures"> 
            
            <Vqiniu></Vqiniu>

    </yd-cell-group>

    
    <yd-cell-group title="Address"> 
            <yd-cell-item>
                <span slot="left"> </span>
                <input slot="right" type="text" @click.stop="show2 = true" v-model="model2" readonly placeholder="Choose your address">
            </yd-cell-item>
    </yd-cell-group>

        <yd-cityselect
                v-model="show2"
                ref="cityselectDemo"
                :callback="result2"
                :items="district"
                provance="shanghai"
                city="shanghai"
                area=""
        ></yd-cityselect>


   <yd-cell-group title="房屋设施"> 
            <yd-cell-item arrow @click.native="show4 = true">
               <span slot="left">Choose what you have{{checklist2}}</span>
            </yd-cell-item>
    </yd-cell-group>

     <yd-popup v-model="show4" position="bottom" height="60%">
               <yd-checklist align="right" v-model="checklist2">
        <yd-checklist-item val="Bath">
            <div style="height: 50px;line-height: 50px;">Bath</div>
        </yd-checklist-item>
        <yd-checklist-item val="Subway">
            <div style="height: 50px;line-height: 50px;">Subway</div>
        </yd-checklist-item>
        <yd-checklist-item val="3">
            <div style="height: 50px;line-height: 50px;">cool shit</div>
        </yd-checklist-item>
    </yd-checklist>
    <yd-button type="warning" style="margin: 30px;" @click.native="show4 = false">Close Bottom Popup</yd-button>
     </yd-popup>
    
    <yd-cell-group title="Describe your awesome room.. ">
        <yd-cell-item>
            <yd-textarea v-model="desc" slot="right" placeholder="Tell me your bank password" maxlength="100"></yd-textarea>
        </yd-cell-item>
    </yd-cell-group>
      


    <yd-cell-group title="My tips">
            <yd-cell-item type="label">
                <div slot="left">Welcome pet? </div>
                <yd-switch slot="right" v-model="pet"></yd-switch>
            </yd-cell-item>
             <yd-cell-item type="label">
                <div slot="left">I dont like guys? </div>
                <yd-switch slot="right" v-model="guys"></yd-switch>
            </yd-cell-item>
    </yd-cell-group>

        <yd-cell-group title="">
             

                <yd-cell-item arrow>
                    <span slot="left">Gender : </span>
                        <select slot="right">
                            <option value="1">guy</option>
                            <option value="2">girl</option>
                            <option value="3">other</option>
                        </select>
                </yd-cell-item>
            </yd-cell-group>

        </yd-layout>
  </div>
</template>
<script>
import District from './address';

import Vqiniu from '@/components/vqiniu'
import {Radio, RadioGroup,TextArea, CellItem, CellGroup,CitySelect,CheckList,CheckListItem} from 'vue-ydui'
export default {
    components:{
        Radio, 
        RadioGroup,
        CellGroup,
        CellItem,
        Vqiniu,
        CitySelect,
        CheckListItem,
        CheckList,
        TextArea,
        
    },
    data () {
        return {
            rooms:'',
            files:[],

            pet:'',
            guys: '',
            model2: '',
            district: District,
            show2: false,

            show4: false,
            checklist2: [],

            desc:''

        }
    },
    methods: {
        result2(ret) {
            console.log(ret);
            this.model2 = ret.itemName1 + ' ' + ret.itemName2 + ' ' + ret.itemName3;
        }
    }
  
}
</script>




